import uniPopup from '@/components/uni-popup/uni-popup.vue'
import uniPopupDialog from '@/components/uni-popup/uni-popup-dialog.vue'
import {
	mapState,
	mapMutations,

} from "vuex" 
export default {
	computed:mapState(['hasLogin','userName','userLogo']),
	data() {
		return {
			content:"确定退出登录",
			isClick:true,
		}
	},
	onLoad: function() {
	},
	methods:{
		...mapMutations(['logout']),
		toLogin(){
			uni.navigateTo({
				url: '/pages/my/login/index',
				animationType: 'pop-in',
				animationDuration: 200,
			});
		},
		outLogin(){
			this.$refs.popup.open();
		},
		confirm(done, value){
			this.logout();
			uni.setStorageSync('user_isLogin', false); //改变用户登录状态
			let user_code = uni.getStorageSync('wx_userCode');//获取用户code
			uni.removeStorageSync('wx_userCode');
			uni.removeStorageSync(user_code);
			done();
		},
		close(done) {
			done();
		},
		clicIsLogin(){
			if( this.hasLogin ){
				this.isClick = false ;
			}
		}
	},
	components: {
		uniPopup,
		uniPopupDialog
	},
}
